import * as types from '../actions/ActionTypes';

const initState = {
    id: null,
    books: []
};

const userCtr = (state = initState, action) => {
    switch (action.type) {
        case types.USER_LOGIN:
            return {
                ...state,
                id: action.id
            }
        case types.USER_LOGOUT:
            return {
                ...state,
                id: null,
                books: null
            }
        case types.FETCH_BOOKS:
            return {
                ...state,
                books: action.books
            }
        case types.ADD_BOOKS:
            return {
                ...state,
                books: [...state.books, action.books]
            }
        case types.REMOVE_BOOKS:
            return {
                ...state,
                books: state.books.filter(action => action.books !== state.books)
            }
        default:
            break;
    }
}

export default userCtr;