import * as types from './ActionTypes';

export const login = (id) => ({ type: types.USER_LOGIN, id })
export const logout = () => ({ type: types.USER_LOGOUT })
export const fetch_books = (books) => ({ type: types.FETCH_BOOKS, books });
export const add_books = (books) => ({ type: types.ADD_BOOKS, books });
export const remove_books = (books) => ({ type: types.REMOVE_BOOKS, books });