import React, { Component } from 'react';
import { Header, PopupMain } from '../../components';
import { Login, Signup } from '../';
import { Button, Popup, Card, Icon, Loader } from 'semantic-ui-react';
import { connect } from 'react-redux';
import * as actions from '../../actions';
import * as checkUser from '../../services/users';

class HeaderContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            popupVisibility: false,
            login: false,
            PopupLabel: null,
            id: null,
            book: []

        };
    }

    // shouldComponentUpdate(nextProps, nextState) {
    //     const a = nextProps !== this.props;
    //     const b = nextState.login !== this.state.login;
    //     const c = nextState.book !== this.state.book;
    //     const d = nextState.PopupLabel !== this.state.PopupLabel;
    //     return b || d;
    // }


    componentWillUpdate(nextProps, nextState) {
        console.log('componentWillUpdate');


        if (this.props.redux)
            if (JSON.stringify(this.props.redux.books) !== JSON.stringify(this.state.book)) {
                this.props.onFetch(nextState.book);
                console.log('redux changed');
            }

        if (JSON.stringify(this.state.book) !== JSON.stringify(nextState.book)) {
            this.props.onFetch(nextState.book)
            console.log('state book changed');
        }

    }

    componentWillReceiveProps(nextProp) {

        console.log('componentWillReceiveProps');
        if (nextProp.redux) console.dir(nextProp.redux);
        const { id } = nextProp.redux;
        if (id != null) {
            this.setState({
                id,
                login: true,
                popupVisibility: false,
            })

            Promise.resolve(checkUser.getBook(id))
                .then(value => {
                    if (!value.data) {
                        return
                    }


                    if (!Array.isArray(value.data)) {
                        const { movie_title, theater_name, movie_date } = value.data;
                        const book = `${movie_title}, ${theater_name}, ${movie_date.slice(0, 10)}`;
                        if (JSON.stringify(this.state.book) !== JSON.stringify(book)) {
                            this.setState({ book });
                            console.log('h8');
                        }
                        // console.log('data not array');
                    } else {
                        // console.log('data is array');
                        const book = value.data.map((a, i) => {
                            const { movie_title, theater_name, movie_date } = a;
                            return (
                                `${movie_title}, ${theater_name}, ${movie_date.slice(0, 10)}`
                            )
                        })

                        if (JSON.stringify(this.state.book) !== JSON.stringify(book)) {
                            this.setState({ book });
                            this.props.onFetch(book);
                            console.log('h9');
                        }
                    }

                })
        }

    }


    clickHandler = (e) => {
        if (e.target.name === 'close') {
            this.setState({
                popupVisibility: false
            })
        } else if (e.target.name === "signup") {
            this.setState({
                popupVisibility: true,
                PopupLabel: < Signup close={this.clickHandler} />
            })
        } else if (e.target.name === "logout") {
            this.setState({
                id: null,
            })

            setTimeout(() => {
                this.setState({
                    login: false,
                });
                this.props.onLogout();
            }, 500);

        }
        else {
            this.setState({
                popupVisibility: true,
                PopupLabel: <Login close={this.clickHandler} />
            })
        }
    }

    render() {

        const { popupVisibility, PopupLabel } = this.state;
        return (
            <Header >
                <ButtonSet login={this.state.login} id={this.state.id} clickHandler={this.clickHandler} book={this.state.book} />
                < PopupMain visible={popupVisibility} >
                    {PopupLabel}
                </PopupMain>
            </Header>
        );
    }
}



class ButtonSet extends Component {
    componentWillReceiveProps() {
        console.log('ButtonSet R P');
    }
    componentWillUpdate() {
        console.log('ButtonSet W U');
    }
    render() {
        const { login, id, clickHandler, book } = this.props;
        return (
            login ? (
                <>
                    <Popup
                        trigger={<Button content='MyStatus' icon="user" color="teal" />}
                        content={
                            <Card>
                                <Card.Content>
                                    <Card.Header>
                                        <Icon name='user' />
                                        Your ID :  {id}
                                    </Card.Header>
                                    <Card.Meta>
                                        <span className='date'>Book List</span>
                                    </Card.Meta>
                                    <ConBookInfo book={book} user_id={id} />

                                </Card.Content>
                                <Card.Content extra>
                                    <Button content='Log Out' name="logout" onClick={clickHandler} color="teal" />
                                    {id ? <></> : <Loader active inline />}
                                </Card.Content>
                            </Card>
                        }
                        on='click'
                        position='top right'
                    />
                </>

            ) : (
                    <Button.Group >
                        <Button content='Sign in' name="signin" onClick={clickHandler} color="teal" />
                        <Button content='Get started' name="signup" onClick={clickHandler} color="teal" />
                    </Button.Group>
                )
        );
    }
};



class BookInfo extends Component {

    hc = (book, key) => {

        console.log(this.props.onRemove);
        const movie_title = book.split(',')[0];
        const theater_name = book.split(', ')[1];
        const movie_date = book.split(', ')[2];
        Promise.resolve(checkUser.removeBook(movie_title, theater_name, movie_date, this.props.user_id))
            .then(
                this.props.onRemove(book)
            );

    }

    componentWillUpdate() {
        console.log('Book info cwu');
    }
    componentWillReceiveProps() {
        console.log('Book info cwrp');
    }
    render() {
        const { book } = this.props;
        let t;
        if (Array.isArray(book)) {
            t = book.map((a, i) => (
                <Card.Description key={i}>
                    {a}
                    <Icon link name='close' onClick={() => this.hc(a, i)} />
                </Card.Description>
            ))
        }

        return (
            <Card.Description>
                {t ? t : book}
            </Card.Description>
        );
    }

}

const ConBookInfo = connect(null, dispatch => ({
    onRemove: (book) => { dispatch(actions.remove_books(book)); console.log(book) }
}))(BookInfo)


export default connect(state => ({ redux: state }), dispatch => ({
    onLogout: () => dispatch(actions.logout()),
    onFetch: (book) => dispatch(actions.fetch_books(book))
}))(HeaderContainer);
