import React, { Component } from 'react';
import { Form, Button, Header, Label, Icon } from 'semantic-ui-react';
import * as checkUser from '../../services/users';
import { connect } from 'react-redux';
import * as actions from '../../actions';


class Login extends Component {
    state = {
        id: null,
        password: null,
        fetching: false,
        login: false,
        error: false,
    }


    fetchId = async (Reqid, ReqPassword) => {
        this.setState({
            fetching: true // requesting..
        });

        try {
            // wait for two promises
            const info = await Promise.all([
                checkUser.getID(Reqid, ReqPassword)
            ]);

            // Object destructuring Syntax,
            // takes out required values and create references to them
            const result = info[0].data;
            if (result === 'OK') {
                this.setState({ login: true, error: false, trueId: this.state.id });
            this.props.onLogin(this.state.trueId)
            }
            else {
                this.setState({ error: true });
            }


            this.setState({
                fetching: false // done!
            });

        } catch (e) {
            // if err, stop at this point
            this.setState({
                fetching: false
            });
        }
    }

    changeHandler = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });

    }

    signInHandler = (e) => {

        if (!e.target.name) return;
        if (e.target.name === 'close') return;
        this.fetchId(this.state.id, this.state.password);
        e.preventDefault()
    }

    render() {
        const { error } = this.state;
        return (
            <>
                <Header as='h2' icon>
                    <Icon name='user' />
                    Member Login
                    <Header.Subheader>Insert Your ID and PassWord</Header.Subheader>
                </Header>
                <Form name="form" onSubmit={this.signInHandler}>
                    {error
                        ?
                        <Form.Field className="Popup-Field" error>
                            <Form.Input icon='user' iconPosition='left' label='Username' placeholder='Username' name="id" onChange={e => this.changeHandler(e)} />
                            <Form.Input icon='key' iconPosition='left' label='Password' placeholder='Password' name="password" onChange={e => this.changeHandler(e)} type="password" />
                            <Label basic color='red' pointing>   Check Again </Label>
                        </Form.Field>
                        :
                        <Form.Field className="Popup-Field" >
                            <Form.Input icon='user' iconPosition='left' label='Username' placeholder='Username' name="id" onChange={e => this.changeHandler(e)} />
                            <Form.Input icon='key' iconPosition='left' label='Password' placeholder='Password' name="password" onChange={e => this.changeHandler(e)} type="password" />
                        </Form.Field>
                    }
                    <Button className="Popup-Submit" content='Sign in' color="teal" type="submit" />
                    <Button content='Cancel' color="red" name="close" type="button" onClick={this.props.close} />
                </Form>
            </>
        );
    }
}

export default connect(null, dispatch => ({
    onLogin: (id) => dispatch(actions.login(id))
}))(Login)