import axios from 'axios';

export function getID(paramId, paramPassword) {
    return axios.get(`/api/id`, {
        params: {
            id: paramId,
            password: paramPassword
        }
    });
}

export function setID(paramId, paramPassword) {
    return axios.post(`/api/id`, {
        params: {
            id: paramId,
            password: paramPassword
        }
    });
}

export function getBook(paramId) {
    return axios.get(`/api/bookId`, {
        params: {
            id: paramId
        }
    });
}

export function setBook(paramId) {
    return axios.post(`/api/bookId`, {
        params: {
            id: paramId
        }
    });
}


export function removeBook(movie_title, theater_name, movie_date, user_id) {
    return axios.post(`/api/deleteB`, {
        params: {
            movie_title, theater_name, movie_date, user_id
        }
    });
}

export function addBook(movie_title, theater_name, movie_date, user_id) {
    return axios.post(`/api/addB`, {
        params: {
            movie_title, theater_name, movie_date, user_id
        }
    });
}